html {
  height: 100%;
}
body {
  min-height: 100%;
  line-height: 1;
  font-family: sans-serif;
}

#frontRowOne {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  min-width: 25vw;
  background-color: black;
}

#frontRowTwo {
  min-width: 50vw;
}

#frontRowThree {
  min-width: 25vw;
  background-color: black;
}

#headerContainer {
  background-color: black;
}

/* preloaded react stuff */

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
